import toast from 'react-hot-toast';

const formatter = new Intl.NumberFormat('id');

export default formatter;

export function roundNumber(number) {
  return Math.floor(number / 100) * 100;
}

export function turnPositive(number) {
  return Math.abs(number);
}

export function showToast(isBuying, price, quantity) {
  if (isBuying) {
    return toast.success(`Voc comprou ${quantity} aes a ${price} reais`, {
      duration: 4000,
      style: {
        borderRadius: '10px',
        background: '#333',
        color: '#fff',
      },
    });
  }
  return toast.success(`Voc vendeu ${quantity} aes a ${price} reais`, {
    duration: 4000,
    style: {
      borderRadius: '10px',
      background: '#333',
      color: '#fff',
    },
  });
}
